import React from 'react';
import contactsService from '../services/contacts'

const Contact = ({ person, setPersons, allPersons, setError }) => {
  const deleteHandler = (e) => {
    if (!window.confirm(`Are you sure you want to delete ${person.firstName} ${person.lastName} from your contacts?`)) return;
    contactsService.remove(person.id)
                   .then(() => {
                    setPersons(allPersons.filter(p => p.id !== person.id));
                   })
                   .catch(err => {
                    setError(err);
                    setTimeout(setError.bind(null, null), 4000);
                  });
  }

  const updateHandler = (e) => {
    
  }

  return (
    <div className='contact'>
      <h3>{person.firstName}{'\u00a0'}{person.lastName}: {person.phoneNumber}</h3>
      <button onClick={deleteHandler}>Delete</button>
      <button onClick={updateHandler}>Update</button>
    </div>
  );
}

export default Contact